  /**
   * 生命周期函数--监听页面加载
   */
  onLoad: function (options) {
    
    let that = this;
    let json = [];
    let prevJson = [];
    let cityJson = [];

    let cityUrl;

    if (options.src) {
      that.setData({
        src: options.src
      });
    } else {
      that.setData({
        src: app.globalData.sourceMark
      });
    }

    // 获取缓存城市数据
    wx.getStorage({
      key: 'cityJson',
      success: function (res) {
        let cityJsonNew = res.data;
        that.setData({ prev: cityJsonNew.prev, city: cityJsonNew.city });
        app.getMyPosition(cityJsonNew, that)
      },
      // 获取缓存失败
      fail: function () { 
        // ajax请求数据并且数据本地缓存存储
        wx.request({
          url: apiUrl + '/city/allcity?level=2',
          header: {
            'content-type': 'application/json'
          },
          success: function (res) {
            that.setData({ prev: res.data.prev, city: res.data.city,  json: res.data.json });
            app.getMyPosition(res.data, that)
            wx.setStorage({
              key: 'cityJson',
              data: { prev: res.data.prev, city: res.data.city,  json: res.data.json },
            })
          }
        })
      }
    });
    // 随机数
    let timer = setInterval(function () {
      let getNum = GetRandomNum(30000, 120000);
      if (getNum > 99999) {
        that.setData({ lingNum: '0000000000', num: getNum });
      } else {
        that.setData({ lingNum: '00000000000', num: getNum });
      }
    }, 400);
  },

