//index.js
//获取应用实例
let app = getApp();
let apiUrl = app.getApiUrl();

Page({
    data: {
        markInfoAll: [],
        lookInfoAll: [],
        isHide: false,
        userInfo: {
          nickName:'点击登录'
        },
        markHide: false,
        lookHide: false,
        userId:'',
        login:''
    },
    onShow: function () {
      let that = this;
      wx.getStorage({
        key: 'userInfo',
        success: function (res) {
          that.setData({
            userInfo:res.data,
            userId:res.data.userId
          });
          if(res.data){
            wx.request({
              url: apiUrl + '/zxjt/likestore',
              data: {
                uid: res.data.userId,// userInfo.userId 用户ID
                limit: 1
              },
              header: {
                'content-type': 'application/json'
              },
              success: function (res) {
                if (res.data.data.length > 0) {
                  that.setData({ markInfoAll: res.data.data, markHide: false })
                } else {
                  that.setData({ markInfoAll: [], markHide: true })
                }
              }
            });
          }
        }
      })
      let arrInfo = app.getNewStorage('arrInfoKey');
      if (arrInfo && arrInfo.length > 0) {
          that.setData({ lookInfoAll: arrInfo, lookHide: false })
      } else {
          that.setData({ lookInfoAll: [], lookHide: true })
      }
    },
    /**
     * 用户点击我的收藏模块跳转到收藏详情页
     */
  toUserMark: function () {
    let that = this;
    if (!this.data.userId){
      app.newSq(that,function(data){
        if(data!==0){
          that.setData({
            userInfo: data,
            userId: data.userId
          })
          wx.navigateTo({
            url: '../user_mark/user_mark?userId=' + that.data.userId
          })
        }
      });
    }else{
      wx.navigateTo({
        url: '../user_mark/user_mark?userId=' + that.data.userId
      })
    }
  },
  phoneCall:function(){
    wx.makePhoneCall({
      phoneNumber: '400-6969-336' //拨打400电话
    })
  },
  login:function(){
    let that = this
    if (that.data.userId===""){
      app.newSq(that,function(data){
        if(data!==0){
          that.setData({
            userInfo: data,
            userId: data.userId
          })
        }
        
      });
    }
  }, 
  onShareAppMessage: function (ops) {
    if (ops.from === 'button') {
    }
    return {
      title: '齐装网装修家居',
      path: 'pages/user/user',
      success: function (res) {},
      fail: function (res) {}
    }
  }
  
})
